










import { Component, Vue, Prop } from 'vue-property-decorator'
import HelloWorld from '@/components/HelloWorld.vue' // @ is an alias to /src
import Page from '@/components/common-components/page.vue'
import { getFleetList } from '@/store/modules/bus/actions'
@Component({
  components: {
    HelloWorld,
    Page
  }
})
export default class Home extends Vue {
  $createToast : any
  fleetList = []
  // 声明周期钩子
  model = { inputValue: '', pcaValue: [], dateValue: '' }
  fields =[{ type: 'input', modelKey: 'inputValue', label: 'Input', props: { placeholder: '请输入' }, rules: { required: true } }]
  valid = undefined
  validity = {}
  page = 1
  limit = 10
  count = 0
  created () {
    this.getFleetList({})
  }

  @Prop({ default: 'default value' }) readonly propB!: string

  // 计算属性
  get showMore () {
    return Math.ceil(this.count / this.limit) >= (this.page + 1)
  }
  getFleetList (obj : any) {
    let skip = obj.skip || 0
    let limit = obj.limit || 10
    const toast = this['$createToast']({
      time: 0,
      txt: '加载...'
    })
    toast.show()
    this.$store.dispatch('getFleetList', { skip: skip, limit: limit }).then((response) => {
      if (response.success) {
        this.fleetList = this.fleetList.concat(response.data.list)
        this.count = response.data.count
      }
      toast.hide()
    })
  }
  pageTurning () {
    if (!this.showMore) {
      return
    }
    this.page++
    let skip = (this.page - 1) * this.limit
    this.getFleetList({ skip: skip })
  }
  toCar (fleetObj : any) {
    this.$router.push({ path: '/car', query: { fleetId: fleetObj.id, title: fleetObj.name } })
  }
}
