










import { Component, Vue, Prop } from 'vue-property-decorator'
import HelloWorld from '@/components/HelloWorld.vue' // @ is an alias to /src
import Page from '@/components/common-components/page.vue'
import { getFleetList } from '@/store/modules/bus/actions'
@Component({
  components: {
    HelloWorld,
    Page
  }
})
export default class Home extends Vue {
  $createToast : any
  carList = []
  // 声明周期钩子
  page = 1
  limit = 10
  count = 0
  fleetId : any
  title: any
  activated () {
    if (this.fleetId === this.$route.query.fleetId) {
      return
    }
    this.carList = []
    this.fleetId = this.$route.query.fleetId
    this.title = this.$route.query.title
    this.getCarList({})
  }
  deactivated () {
    this.page = 1
    this.count = 0
  }

  @Prop({ default: 'default value' }) readonly propB!: string

  // 计算属性
  get showMore () {
    return Math.ceil(this.count / this.limit) >= (this.page + 1)
  }
  getCarList (obj : any) {
    let skip = obj.skip || 0
    let limit = obj.limit || 10
    let fleetId = this.fleetId
    const toast = this['$createToast']({
      time: 0,
      txt: '加载...'
    })
    toast.show()
    this.$store.dispatch('getCarList', { fleetId: fleetId, skip: skip, limit: limit }).then((response) => {
      if (response.success) {
        this.carList = this.carList.concat(response.data.list)
        this.count = response.data.count
      }
      toast.hide()
    })
  }
  pageTurning () {
    if (!this.showMore) {
      return
    }
    this.page++
    let skip = (this.page - 1) * this.limit
    this.getCarList({ skip: skip })
  }
  toCarInfo (carObj : any) {
    this.$router.push({ path: '/carInfo', query: { carId: carObj.id } })
  }
}
